*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.alert-primary {
  background-color: red;
}

.my-card {
  margin-bottom: 20px;
}

.content {
  height: 100px;
  border-radius: 10px;
  background-color: crimson;
}